import { buildUrl } from '../../Services/Utils/Utility'

/**
 * Fetch data
 *
 * @param {string} api
 * @param {string} method
 * @returns {Object}
 */
export function fetchData(api, method = 'GET', params = {}) {
    return fetch(
        buildUrl(`http://localhost:8000/api/${api}`, params),
        {
            method: `${method}`,
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json"
            },
        }).then(response => response.json())
        .then(responseData => {
            if (responseData.code === 200) {
                return responseData.data
            }
        })
        .catch(error => console.log(error));
}

/**
 * Submit data
 *
 * @param {string} api
 * @param {string} method
 * @param {object} data
 * @returns {Object}
 */
export function submitData(api, method = 'POST', data) {
    return fetch(`http://localhost:8000/api/${api}`, {
        method: `${method}`,
        body: JSON.stringify(data),
        headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
        },
    }).then(response => response.json())
        .then(responseData => {
            if (responseData.code === 200) {
                return true;
            } else {
                return false;
            }
        })
        .catch(error => console.log(error));
}

/**
 * Update data
 *
 * @param {string} api
 * @param {object} data
 * @param {number} id
 * @returns {Object}
 */
export function updateData(api, data, id) {
    return fetch(`http://localhost:8000/api/${api}/${id}`, {
        method: 'PUT',
        body: JSON.stringify(data),
        headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
        },
    }).then(response => response.json())
        .then(responseData => {
            if (responseData.code === 200) {
                return true;
            } else {
                return false;
            }
        })
        .catch(error => console.log(error));
}